 
import { Routes, RouterModule } from '@angular/router';
import { NgModule } from '@angular/core';

import { LoginComponent, UserRegistrationComponent, RegistrationAcknowledgementComponent } from './index';
import { UserResolver } from '../shared/services/common/user-resolver.service';

const accountRoutes: Routes = [
    {
        path: '', component: LoginComponent
    },
    {
        path: 'userRegistration', component: UserRegistrationComponent, resolve: { user: UserResolver }
    },
    {
        path: 'registrationAcknowledgement', component: RegistrationAcknowledgementComponent
    }
];

@NgModule({
    imports: [RouterModule.forChild(accountRoutes)],
    exports: [RouterModule],
})
export class AccountRoutingModule {
}



